@import "src/styles/common.scss";

.text_button {
  border-radius: 10px;
  border: 1px solid $blue-bay;
  padding: 10px 20px;
  color: $blue-malibu;
  font-size: 17px;
  font-weight: 500;
  cursor: pointer;
  width: auto;
}

.btn_transparent {
  background-color: transparent;
}
.btn_primary {
  background-color: $pink-wisteria;
  color: white;
}
